import React, { Component } from 'react';
import { NavBar,Icon,SearchBar,Carousel,TabBar } from 'antd-mobile';
import { HashRouter as Router, Route } from "react-router-dom";
import {withRouter} from 'react-router-dom';
import 'antd-mobile/dist/antd-mobile.css';
import {array} from './actionCreator';
import {connect} from 'react-redux';

const Articles = (props) =>{
  console.log(props);
  props.dispatch(array());
  return <div>
  {
    props.array.map((item)=>{
      return <div style={{width:'45%',float:'left',border:'1px white solid',
      margin:'8px 8px',paddingBottom:'10px',background:'white'
      }}>
      <div 
      onClick={()=>{ props.history.push({
        pathname:'/home/detail',
        img:item.img,title:item.title,id:item.id
      })}} >
        <img src={item.img} style={{width:'100%'}} />
        <p>{item.title}</p>
        <p>{item.price}¥</p>
      </div>
      </div>
    })
  }
  </div>
};
const mapStateToProps = (state) =>({
  array:state.array.data, 
});
const Viewlist=withRouter(Articles);
export default connect(mapStateToProps)(Viewlist);
