import React,{useState} from 'react'
import { connect } from 'react-redux'
import { ActivityIndicator } from 'antd-mobile'
import {login} from './actionCreator'

const Collect = (props) => {
    const [isShow,setShow] = useState(false);
    const log = () => {
        props.dispatch(login())
    } 
    return (
        <div>
            {
                props.username
                    ? <div>
                        {/* { props.username }
                        <button
                            onClick={ log }
                        >退出</button> */}
                        {props.history.push('/shoucang')}
                    </div>
                    : <div>
                        <div style={{width:'110px',height:'30px',margin:'90px 0px 10px 30%'}}>
                        <input placeholder="用户名"/></div>
                        <div style={{width:'110px',height:'30px',margin:'0px 0px 10px 30%'}}>
                        <input placeholder="密码"/></div>
                        <button
                            onClick={ log }
                            style={{width:'60px',height:'30px',margin:'0px 0px 0px 44%'}}
                        >登录</button>
                    </div>
            }
            <ActivityIndicator 
                toast
                text="登录中..."
                animating={isShow}
            />
        </div>
    )
}
const mapStateToProps = (state) => ({
    username: state.home.username
})
export default connect(mapStateToProps)(Collect);