import React from 'react';
import 'antd-mobile/dist/antd-mobile.css';
import {HashRouter as Router ,Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile';
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Collect from './taobao/Collect';
import List from './taobao/List';
import Mytabb from './taobao/Mytabb';
import Detail from './taobao/Detail';
import SearchBarr from './taobao/Searchbar';
import { Provider } from 'react-redux';
import store from './taobao/store';
import todos from './taobao/Todos'

const App = () => {
	return <Provider store={store}>
	<Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/collect' component={Collect} />
			<Route path='/list' component={List} />
			<Route path='/mytabb' component={Mytabb} />
			<Route path='/home/searchbar' component={SearchBarr} />
			<Route path='/home/detail' component={Detail} />
			<Route path='/shoucang' component={todos} />
		</Switch>
		<Mytab />
	</Router>
	</Provider>
}

export default App;

