import React, { Component } from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import {withRouter} from 'react-router-dom'
import {NavBar,Icon} from 'antd-mobile'
import 'antd-mobile/dist/antd-mobile.css';

 class search extends Component {
    constructor(props){
        super(props);
        this.state = {}
    }
    
    render() {
        return (
            <div>
                <div
                    style={{paddingTop:'5px',paddingBottom:'6px',
                    backgroundRepeat:'no-repeat',backgroundSize:'28px 21px',
                    backgroundPosition:'9px center',backgroundColor:'rgb(255, 133, 42)',
                    backgroundImage:'url("data:image/png;base64,")'
                }}>
                    <Button icon="search"
                        onClick={()=>{this.props.history.push('/home/searchbar')}}
                        style={{background:'rgb(255, 78, 34)',
                        border:'1px rgb(255, 78, 34) solid',
                        color:'white',width:'86%',height:'28px',
                        marginLeft:'12%',textAlign:'center',
                        borderRadius:'5px'
                    }}>
                      寻找宝贝店铺
                    </Button>
                </div> 
                </div> 
        )
    }
}

export default withRouter(search);