import React from 'react'
import {connect} from 'react-redux'
const Todos = (props) => {

    console.log(props);

    return (
        <div>
            {
                props.array.map((item,idx)=>(
                    <li key={idx}>
                        {item.title}
                        <button
                            onClick={()=>{
                                props.dispatch({
                                    type: "DEL_TODO",
                                    index: idx
                                })
                            }}
                        >删除</button>
                    </li>
                ))
            }
        </div>
    )
}

const mapStateToProps = (state) =>({
    array:state.todo, 
});

export default connect(mapStateToProps)(Todos)