import React from 'react';
import { NavBar,Icon,SearchBar,Carousel,TabBar } from 'antd-mobile';
import { HashRouter as Router, Route } from "react-router-dom";
import {withRouter} from 'react-router-dom';
import 'antd-mobile/dist/antd-mobile.css';

class Mytab extends React.Component{
    render() {
        const { pathname } = this.props.location;

        if(pathname.split('/').length>2){
          return null;
        }
        return (
            <div style={{ 
              position: 'fixed', width: '100%', bottom: 0 
            }}>
            <TabBar
              unselectedTintColor="#949494"
              tintColor="red"
              barTintColor="white"
            >

            <TabBar.Item
              title="首页"
              key="home"
              icon={
                  <i className='iconfont icon-shouye'></i> 
              }
              selectedIcon={
                  <i className='iconfont icon-shouye'></i>  
              }
              selected={ pathname === '/' }
              onPress={ () => {
                  this.props.history.push('/');
              } }
              data-seed="logId"
            >
            </TabBar.Item>
            
            <TabBar.Item
              title="我的收藏"
              key="collect"
              icon={
                  <i className='iconfont icon-shoucang'></i> 
              }
              selectedIcon={
                  <i className='iconfont icon-shoucang'></i>
              }              
              selected={ pathname === '/collect' }
              onPress={ () => {
                  this.props.history.push('/collect')
              } }
              data-seed="logId1"
            > 
            </TabBar.Item>

            <TabBar.Item
              title="我的订单"
              key="list"
              icon={
                <i className='iconfont icon-dingdan'></i>
              }
              selectedIcon={
                <i className='iconfont icon-dingdan'></i>
              }              
              selected={ pathname === '/list' }
              onPress={ () => {
                  this.props.history.push('/list')
              } }
              data-seed="logId2"
            >
            </TabBar.Item>

            <TabBar.Item
              title="我的淘宝"
              key="mytabb"
              icon={<i className='iconfont icon-wode'></i>}
              selectedIcon={ 
                <i className='iconfont icon-wode'></i>
              }                             
              selected={ pathname === '/mytabb' }
              onPress={ () => {
                  this.props.history.push('/mytabb')
              } }
              data-seed="logId3"
            >
            </TabBar.Item>

            </TabBar>
          </div>
        );
    }
}

export default withRouter(Mytab);