import React, { Component } from 'react';
import {BrowserRouter as Router,Switch,privateRoute } from 'react-router-dom'
import { NavBar,Icon,SearchBar, WhiteSpace, TabBar } from 'antd-mobile';
//import Tabdetail from './Tabdetail';
import 'antd-mobile/dist/antd-mobile.css';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

class Detail extends Component {
    constructor(props){
		super(props);
		this.state={
			selectedTab:'blueTab'
		};
	}
    componentDidMount(){
		let id = this.props.location.id;
		console.log(this.props);
        console.log(this.props.array);
        this.props.array.map((item)=>{
            if(item.id===id){
                this.setState({selectedTab:'blueTab'})
            }
        })
	}
    render() {
        let img = this.props.location.img;
        let id = this.props.location.id;
        let title = this.props.location.title;
        //console.log(this.props.location);
        return (
            <div>
                <NavBar
                    icon={<Icon type="left"/>} mode="light"
                    onLeftClick={() => this.props.history.push('/')}
                >商品详情
                </NavBar>              

                <img src={this.props.location.img}
                style={{width:'100%'}} />
                <p style={{fontSize:'20px',textAlign:'center'}}>
                    {this.props.location.title}
                </p>
                {/* <Router>
                    <Tabdetail />
                </Router>         */}
                <div style={{ 
                    position: 'fixed', width: '100%', bottom:0 
                }}>
				<TabBar
					unselectedTintColor="#949494"
					tintColor="red" 
					barTintColor="white" 
				>

				<TabBar.Item
					title="店铺"
					key="shop"
					icon={<i className='iconfont icon-dianpu'></i>}
					selectedIcon={<i className='iconfont icon-dianpu'></i>}					
					//selected={pathname === '/shop'}
					// onPress={() => {
					// 	this.props.history.push('/shop')
					// }
					//}
				data-seed="logId"
				>
				</TabBar.Item>

				<TabBar.Item
					title="客服"
					key="worker"
					icon={<i className='iconfont icon-kefu1'></i>}
					selectedIcon={<i className='iconfont icon-kefu1'></i>}
					// selected={pathname === '/worker'}
					// onPress={() => {
					// 	this.props.history.push('/worker')
					// }
					// }
					data-seed="logId1"
				>
				</TabBar.Item>

				<TabBar.Item
					title="收藏"
					key="shoucang"  
					//onClick={console.log(this.props)}
					selected={this.state.selectedTab==='redTab'}
					icon={ <i className='iconfont icon-shoucang1'></i>}
					selectedIcon={<i className='iconfont icon-shoucang1'></i>}
					//selected={pathname === '/shoucang'}
					onPress={() => {
						this.props.dispatch({
							type:'ADD_TODO',
							col:{img:img,id:id,title:title}
                        })
                        this.setState({
                            selectedTab:'redTab'
                        })
					}}	
					data-seed="logId2"
				>
				</TabBar.Item>

				<TabBar.Item
					icon={<button
					style={{backgroundColor:'rgb(255, 152, 2)',color:'white',
					borderTopLeftRadius:'20px',borderBottomLeftRadius:'20px',
					border:'1px rgb(255, 152, 2) solid',height:'35px',width:'100px'}}>
						加入购物车
					</button>}
					key="addcart" 
				>
				</TabBar.Item>

				<TabBar.Item
					icon={<button className="right"
					style={{backgroundColor:'rgb(254, 92, 8)',color:'white',
					height:'35px',width:'100px',border:'1px rgb(254, 92, 8) solid',
					borderTopRightRadius:'20px',borderBottomRightRadius:'20px'}}>
						立即购买
					</button>}
					key="nowbuy"  
				>
				</TabBar.Item>
			</TabBar>
		</div>
		</div>    
        )
    }
}

const mapStateToProps = (state) =>({
    array:state.array.data, 
});
  
const Viewlist=withRouter(Detail);
export default connect(mapStateToProps)(Viewlist);